.survey {
  /* margin-top: 1rem; */
  /* width: 70%; */
  margin-right: auto;
  margin-left: auto;
  /* display: block; */
  /* grid-template-columns: repeat(3fr);
    grid-template-rows: 100px 100px; */

  /* text-align: center; */
  /* background-color: pink; */
  margin-bottom: 1.5rem;
  display: grid;
  justify-items: center;
}

.survey .title {
  color: #000;
  line-height: 65px;
  text-align: center;
  position: relative;
  font-size: 18px;
  font-weight: 600;
}
.wrapper .box {
  padding: 20px 30px;
  border-radius: 10px;
  width: 300px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: relative;
  column-gap: 1.7rem;
}
.wrapper .survey-right {
  margin: 10px;
}

.wrapper .box label {
  display: flex;
  height: 23px;
  align-items: center;
  margin: 10px 0;
  padding-left: 9px;
  padding-right: 2px;
  cursor: default;
  transition: all 0.3s ease;
}
#option-1:checked ~ .option-1,
#option-2:checked ~ .option-2,
#option-3:checked ~ .option-3,
#option-4:checked ~ .option-4,
#option-5:checked ~ .option-5,
#option-6:checked ~ .option-6 {
  background: rgba(72, 98, 85, 0.7);
  border-radius: 4px;
}

.wrapper .box label .dot {
  height: 20px;
  width: 20px;
  background: #d9d9d9;
  border-radius: 50%;
  position: relative;
  transition: all 0.3s ease;
}

#option-1:checked ~ .option-1 .dot,
#option-2:checked ~ .option-2 .dot,
#option-3:checked ~ .option-3 .dot,
#option-4:checked ~ .option-4 .dot,
#option-5:checked ~ .option-5 .dot,
#option-6:checked ~ .option-6 .dot {
  background: #fff;
}

.box label .dot::before {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(2);
  width: 9px;
  height: 9px;
  border-radius: 50%;
  transition: all 0.3s ease;
}

#option-1:checked ~ .option-1 .dot::before,
#option-2:checked ~ .option-2 .dot::before,
#option-3:checked ~ .option-3 .dot::before,
#option-4:checked ~ .option-4 .dot::before,
#option-5:checked ~ .option-5 .dot::before,
#option-6:checked ~ .option-6 .dot::before {
  background: #486255;
  transform: translate(-50%, -50%) scale(1);
}
.wrapper .box label .text {
  color: #333;
  font-size: 16px;
  font-weight: 400;
  padding-left: 10px;
  transition: color 0.3s ease;
}
#option-1:checked ~ .option-1 .text,
#option-2:checked ~ .option-2 .text,
#option-3:checked ~ .option-3 .text,
#option-4:checked ~ .option-4 .text,
#option-5:checked ~ .option-5 .text,
#option-6:checked ~ .option-6 .text {
  color: #fff;
}
.wrapper .box input[type='radio'] {
  display: none;
}

.form-group {
  margin-bottom: 2rem;
  height: 5rem;
  text-align: center;
}
.question-container .form-group button {
  padding: 6px;
  background-color: #486255;
  color: #fff;
  font-size: 15px;
  outline: none;
  border-radius: 3px;
  width: 7rem;
  margin-bottom: 1rem;
}

#thank-you {
  font-size: 25px;
  color: blue;
}

/* media screen */
@media screen and (max-width: 1024px) {
  .contact-main .bg-image {
    height: 250px;
  }
  .contact-main .bg-text {
    top: 420px;
  }
}
@media screen and (max-width: 920px) {
  .contact-main .bg-image {
    height: 200px;
  }

  .bg-text {
    top: 36%;
    left: 30%;
  }
  .contact-survey-wrapper {
    max-width: 95%;
  }
}
@media screen and (max-width: 768px) {
  .contact-main {
    font-size: 15px;
  }
  .contact-main .bg-image {
    width: 100%;
  }
  .contact-main .bg-text {
    top: 25%;
    width: 43%;
    padding: 16px;
  }

  .input,
  .msg .area {
    padding: 10px;
  }

  .btn {
    width: 160px;
    height: 40px;
    line-height: 15px;
    border-radius: 5px;
  }
  .login .input {
    width: 100%;
  }
}
@media screen and (max-width: 680px) {
  .contact-main .bg-text {
    top: 23%;
    width: 80%;
  }

  .survey .title {
    font-size: 16px;
  }
  .wrapper .box {
    flex-wrap: wrap;
  }
  .wrapper .box label {
    height: 18px;
    margin: 6px 0;
    padding-left: 5px;
  }
}
@media screen and (max-width: 480px) {
  .contact-main .bg-text {
    top: 30%;
    width: 90%;
  }
}
